/* istanbul ignore file */
import { function as F, option as O, pipeable as P } from "fp-ts";
export function isCons(list) {
    return list._tag === "cons";
}
export function isNil(list) {
    return list._tag === "nil";
}
export const nil = { _tag: "nil" };
export function cons(h, t) {
    return {
        _tag: "cons",
        head: h,
        tail: t
    };
}
export function of(a) {
    return cons(a, nil);
}
export function foldl(list, b, f) {
    let iter = list;
    let seed = b;
    while (isCons(iter)) {
        seed = f(seed, iter.head);
        iter = iter.tail;
    }
    return seed;
}
export function foldlc(b, f) {
    return (list) => foldl(list, b, f);
}
export function reverse(list) {
    return foldl(list, nil, (t, h) => cons(h, t));
}
export function foldr(list, b, f) {
    return P.pipe(list, reverse, foldlc(b, F.flip(f)));
}
export function snoc(append, list) {
    return foldr(list, of(append), cons);
}
export function cata(list, ifCons, ifNil) {
    if (isCons(list)) {
        return ifCons(list.head, list.tail);
    }
    return ifNil();
}
export function catac(ifCons, ifNil) {
    return (list) => cata(list, ifCons, ifNil);
}
export function head(list) {
    return cata(list, O.some, () => O.none);
}
export function tail(list) {
    return cata(list, (_, rest) => O.some(rest), () => O.none);
}
export function last(list) {
    if (isNil(list)) {
        return O.none;
    }
    let iter = list;
    while (isCons(iter.tail)) {
        iter = iter.tail;
    }
    return O.some(iter.head);
}
export const isEmpty = isNil;
export const nonEmpty = F.not(isNil);
export function find(list, f) {
    let iter = list;
    while (isCons(iter)) {
        if (f(iter.head)) {
            return O.some(iter.head);
        }
        iter = iter.tail;
    }
    return O.none;
}
export function findc(f) {
    return (list) => find(list, f);
}
export function foldrc(b, f) {
    return (list) => foldr(list, b, f);
}
export function map(list, f) {
    return P.pipe(list, foldlc(nil, (t, a) => cons(f(a), t)), reverse);
}
export function lift(f) {
    return (list) => map(list, f);
}
export function filter(list, f) {
    return foldr(list, nil, (a, t) => (f(a) ? cons(a, t) : t));
}
export function filterc(f) {
    return (list) => filter(list, f);
}
export function concat(front, back) {
    return foldr(front, back, cons);
}
/**
 * Get the size of a list.
 *
 * This has pathologically bad performance.
 * @param list
 */
export function size(list) {
    let ct = 0;
    let iter = list;
    while (isCons(iter)) {
        ct++;
        iter = iter.tail;
    }
    return ct;
}
export function chain(list, f) {
    return P.pipe(list, lift(f), foldlc(nil, concat));
}
export function ap(list, fns) {
    return chain(list, (a) => map(fns, (f) => f(a)));
}
export function flatten(list) {
    return foldl(list, nil, concat);
}
export function fromArray(as) {
    return as.reduceRight((t, h) => cons(h, t), nil);
}
export function toArray(as) {
    const out = [];
    let iter = as;
    while (isCons(iter)) {
        out.push(iter.head);
        iter = iter.tail;
    }
    return out;
}
export const URI = "matechs/WaveList";
export const instances = {
    URI,
    map,
    of,
    ap: F.flip(ap),
    chain
};
