import { either as E, function as F, option as O, pipeable as P } from "fp-ts";
import { makeDeferred } from "./deferred";
import { makeRef } from "./ref";
import { natNumber } from "./sanity";
import { makeSemaphore } from "./semaphore";
import { empty, of } from "./original/support/dequeue";
import { makeTicket, ticketExit, ticketUse } from "./ticket";
import * as T from "./effect";
import { effect } from "./effect";
const initial = () => E.right(empty());
const poption = P.pipeable(O.option);
const unboundedOffer = (queue, a) => queue.offer(a);
// TODO: Need a better way of checking for this
// Possibly predicates that allow testing if the queue is at least of some size
const slidingOffer = (n) => (queue, a) => queue.size() >= n
    ? P.pipe(queue.take(), poption.map((t) => t[1]), O.getOrElse(() => queue)).offer(a)
    : queue.offer(a);
const droppingOffer = (n) => (queue, a) => queue.size() >= n ? queue : queue.offer(a);
function makeConcurrentQueueImpl(state, factory, overflowStrategy, 
// This is effect that precedes offering
// in the case of a boudned queue it is responsible for acquiring the semaphore
offerGate, 
// This is the function that wraps the constructed take IO action
// In the case of a bounded queue, it is responsible for releasing the
// semaphore and re-acquiring it on interrupt
takeGate) {
    function cleanupLatch(latch) {
        return T.asUnit(state.update((current) => P.pipe(current, E.fold((waiting) => E.left(waiting.filter((item) => item !== latch)), (available) => E.right(available)))));
    }
    const take = takeGate(T.bracketExit(effect.chain(factory, (latch) => state.modify((current) => P.pipe(current, E.fold((waiting) => [
        makeTicket(latch.wait, cleanupLatch(latch)),
        E.left(waiting.offer(latch))
    ], (ready) => P.pipe(ready.take(), poption.map(([next, q]) => [makeTicket(T.pure(next), T.unit), E.right(q)]), O.getOrElse(() => [
        makeTicket(latch.wait, cleanupLatch(latch)),
        E.left(of(latch))
    ])))))), ticketExit, ticketUse));
    const offer = (a) => T.applySecond(offerGate, T.uninterruptible(T.flatten(state.modify((current) => P.pipe(current, E.fold((waiting) => P.pipe(waiting.take(), poption.map(([next, q]) => [next.done(a), E.left(q)]), O.getOrElse(() => [T.unit, E.right(overflowStrategy(empty(), a))])), (available) => [T.unit, E.right(overflowStrategy(available, a))]))))));
    return {
        take,
        offer
    };
}
/**
 * Create an unbounded concurrent queue
 */
export function unboundedQueue() {
    return effect.map(makeRef(initial()), (ref) => makeConcurrentQueueImpl(ref, makeDeferred(), unboundedOffer, T.unit, F.identity));
}
const natCapacity = natNumber(new Error("Die: capacity must be a natural number"));
/**
 * Create a bounded queue with the given capacity that drops older offers
 * @param capacity
 */
export function slidingQueue(capacity) {
    return T.applySecond(natCapacity(capacity), effect.map(makeRef(initial()), (ref) => makeConcurrentQueueImpl(ref, makeDeferred(), slidingOffer(capacity), T.unit, F.identity)));
}
/**
 * Create a dropping queue with the given capacity that drops offers on full
 * @param capacity
 */
export function droppingQueue(capacity) {
    return T.applySecond(natCapacity(capacity), effect.map(makeRef(initial()), (ref) => makeConcurrentQueueImpl(ref, makeDeferred(), droppingOffer(capacity), T.unit, F.identity)));
}
/**
 * Create a bounded queue that blocks offers on capacity
 * @param capacity
 */
export function boundedQueue(capacity) {
    return T.applySecond(natCapacity(capacity), T.effect.zipWith(makeRef(initial()), makeSemaphore(capacity), (ref, sem) => makeConcurrentQueueImpl(ref, makeDeferred(), unboundedOffer, sem.acquire, (inner) => 
    // Before take, we must release the semaphore. If we are interrupted we should re-acquire the item
    T.bracketExit(sem.release, (_, exit) => (exit._tag === "Interrupt" ? sem.acquire : T.unit), () => inner))));
}
