import * as T from "./effect";
export function ticketExit(ticket, exit) {
    if (exit._tag === "Interrupt") {
        return ticket.cleanup;
    }
    return T.unit;
}
export function ticketUse(ticket) {
    return ticket.acquire;
}
export function makeTicket(acquire, cleanup) {
    return {
        acquire,
        cleanup
    };
}
