/* istanbul ignore file */
import { option as O, pipeable as P } from "fp-ts";
import * as l from "./list";
import { cons, nil } from "./list";
class DequeueImpl {
    constructor(front, back) {
        this.front = front;
        this.back = back;
    }
    take() {
        return l.cata(this.front, (h, t) => O.some([h, new DequeueImpl(t, this.back)]), () => P.pipe(this.back, l.reverse, l.catac((h, t) => O.some([h, new DequeueImpl(t, nil)]), () => O.none)));
    }
    offer(a) {
        return new DequeueImpl(this.front, cons(a, this.back));
    }
    pull() {
        return l.cata(this.back, (h, t) => O.some([h, new DequeueImpl(this.front, t)]), () => P.pipe(this.front, l.reverse, l.catac((h, t) => O.some([h, new DequeueImpl(nil, t)]), () => O.none)));
    }
    push(a) {
        return new DequeueImpl(cons(a, this.front), this.back);
    }
    filter(p) {
        return new DequeueImpl(l.filter(this.front, p), l.filter(this.back, p));
    }
    size() {
        return l.size(this.front) + l.size(this.back);
    }
    isEmpty() {
        return l.isEmpty(this.front) && l.isEmpty(this.back);
    }
    find(p) {
        return O.option.alt(l.find(this.front, p), () => l.find(this.back, p));
    }
}
export function from(front, back) {
    return new DequeueImpl(front, back);
}
export function empty() {
    return from(nil, nil);
}
export function of(a) {
    return from(l.of(a), nil);
}
